.alerta{

    padding: .5rem;
    text-align: center;
    color: $blanco;
    font-weight: bold;
    text-transform: uppercase;
    margin: 1rem 0;
    border-radius: 1rem;

    &.error{
        background-color: $rojo;
    }

    &.exito{
        background-color: $verde;
    }
}

table.productos{
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;
    text-align: center;
    margin-bottom: 4rem;

    thead{
        background-color: $cuarto;

        th{
            color: $blanco;
            padding: 2rem;
        }
    }

    .imagen-tabla{
        width: 10rem;
    }
}

.formulario{
    .imagen-small{
        width: 20rem;
    }
}

.enlace-producto:hover{
    cursor: pointer;
}